import { Gltf, useGLTF } from "@react-three/drei";
import { useFrame } from "@react-three/fiber";
import { ghostModel } from "some-3d-models";
import { RigidBody } from "@react-three/rapier";
const PlayerModel = ({ position = [0, 0, 0], rotation = [0, 0, 0], scale = 1, onUpdatePosition, playerRef }) => {
  // 加载 GLB 模型
  const { scene } = useGLTF(ghostModel);

  useFrame(() => {
    if (playerRef?.current) {
      const pos = playerRef.current.position;
      // 调用父组件的回调函数更新位置
      onUpdatePosition([pos.x, pos.y, pos.z]);
    }
  });
  const handleModelLoad = (model) => {
    // Assign the loaded model to your ref
    if (playerRef) playerRef.current = model.scene;
  };

  return (
    
    // <primitive
    //   ref={playerRef} // 将 ref 绑定到模型
    //   object={scene}
    //   position={position}
    //   rotation={rotation}
    //   scale={scale}
    //   castShadow
    //   receiveShadow
    // />
 <Gltf  
   src={ghostModel}
    position={position}
 rotation={rotation}
 scale={scale}
 castShadow
 receiveShadow
 onLoad={handleModelLoad} 
  />
  );
};

useGLTF.preload(ghostModel);

export default PlayerModel;
